
.game{
  width: 100%;
  margin: 20vh auto 0;
}
.game-board{
  display: flex;
  flex-direction: row;
  /* height: 100vh; */
  justify-content: center;
  align-items: center;
  /* background-color: #B4B4FF; */

  
}
.game-info{
  margin-top: 20px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.m-button{
  width: 200px;
  font-size: 20px;
  font-family: 'play','sans-serif';
  background: #B4B4FF;
  padding: 5px;
  height: 35px;
  font-style: bold;
  border-radius: 10px;
  box-shadow:3px 5px 1px 2px #5050FF ; 

}
