.square{
    background: #fff;
    border: 1.5px solid #C1AEEE;
    font-size: 60px;
    font-weight: bold;
    width: 100px;
    height: 100px;
    padding: 0;
    text-align: center;
    margin-top: -1px;
    margin-left: -1px;
    font-family: 'play',sans-serif;
    float: left;
    border-radius: 8px;
}
.square:hover{
    background: #FFE3EE;
}